

































import general from '../../../common/utils/general.js'
export default {
	data() {
		return {
			imgList: [],
			pubtext:'人生的磨难是很多的，所以我们不可对于每一件轻微的伤害都过于敏感。在生活磨难面前，精神上的坚强和无动于衷是我们抵抗罪恶和人生意外的最好武器。',
			modalName: null
			
		};
	},
	methods: {
		
		ChooseImage() {
			let that = this;
			let imglist = this.imgList;
			if(imglist.length<6){
				uni.chooseImage({
					count: 1, //默认9
					sizeType: ['original', 'compressed'], //可以指定是原图还是压缩图，默认二者都有
					sourceType: ['album'], //从相册选择
					success: (res) => {
						// if (this.imgList.length != 0) {
						// 	this.imgList = this.imgList.concat(res.tempFilePaths)
						// } else {
						// 	this.imgList = res.tempFilePaths
						// }
						var tempFilePaths = res.tempFilePaths
						console.log(tempFilePaths);
						uni.showLoading({
						  title: '正在上传~',
						})
						// var Len = tempFilePaths.length;
						uni.uploadFile({ //上传图片
						  url: that.global.baseUrl + 'static/doUploadFileMu',
						  filePath: tempFilePaths[0],
						  name: 'file',
						  success: (res)=>{
								console.log(res)
								var data = JSON.parse(res.data);
								var url = this.global.baseUrl+data.url.substring(6)
								console.log(url)
								imglist.push(url);
								// upImg.push(url)  //将服务器返回的路径存到数组
								this.imgList = imglist
								wx.hideLoading();
							//do something
						  }
						})
					}
				});
			}else{
				uni.showToast({
					title:'最多只能上传6张图片',
					icon:'none'
				})
			}
		},
		async handlePub(){ //发表按钮
			let userinfo = uni.getStorageSync('userinfo');
			let token = JSON.parse(userinfo).token;
			let imgs = this.imgList;
			console.log(token)
			let pubObj={
				neirong:this.pubtext
			}
			for(let i=0;i<imgs.length;i++){
				pubObj['newspic'+(i+1)] = imgs[i]
			}
			console.log(pubObj)
			let pubData = await general.generalGET(
				this.global.baseUrl+'DisseminationEducation',
				pubObj,
				{token}
			)
			console.log(pubData)
			if(pubData.data.code=='OK'){
				uni.showToast({
					title:'发布成功',
					icon:'none'
				})
				uni.navigateBack({
					delta:1
				})
			}
		},
		ViewImage(e) {
			uni.previewImage({
				urls: this.imgList,
				current: e.currentTarget.dataset.url
			});
		},
		DelImg(e) {
			this.imgList.splice(e.currentTarget.dataset.index, 1)
			// uni.showModal({
			// 	
			// 	title: '召唤师',
			// 	content: '确定要删除这段回忆吗？',
			// 	cancelText: '再看看',
			// 	confirmText: '再见',
			// 	success: res => {
			// 		if (res.confirm) {
			// 			this.imgList.splice(e.currentTarget.dataset.index, 1)
			// 		}
			// 	}
			// })
		},
		textareaAInput(e) {
			this.textareaAValue = e.detail.value
		},
		textareaBInput(e) {
			this.textareaBValue = e.detail.value
		}
	}
}
